.gpt3__features{
    padding: 4rem 6rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.gpt3__features-heading {
    flex: 2;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}
.gpt3__features-content{
    display: flex;
    justify-content: space-between;
    margin-left: 2rem;
}
.gpt3__features-container{

    display: flex;
    flex-direction: column;
    flex:3;
}
.gpt3__features-content p{
    margin-bottom: 1rem ;
    margin-left: 1rem;
    color: var(--color-text);
    font-size: 14px;
    line-height: 24px;
}
.gpt3__features-heading p{
    margin-top: 4rem;
    color: #FF8A71;
    cursor: pointer;
}
@media screen and (max-width:850px){
    .gpt3__features-content{
        display: flex;
        flex-direction: column;
       margin-left: 0;
    }
.gpt3__features-content p, .gpt3__features-heading p{
    font-size: 12px;
    line-height: 20px;
    margin-left: 0;
}
}
@media screen and (max-width:760px) {
   .gpt3__features{
       display: flex;
       flex-direction: column;
       padding: 3rem 3rem;
   }
}
@media screen and (max-width:400px){
    .gpt3__features-heading h1{
        font-size: 25px;
        text-align: center;
    }
}