.gpt3__possibility{
    padding: 4rem 6rem;
    display: flex;
    align-items: center;
}
.gpt3__possibility img{
    width: 100%;
    height: 100%;
}
.gpt3__possibility-content{
    margin-top: 8rem;
    margin-left: 1rem;
}
.gpt3__possibility-content p:first-child{
    color: #71E5FF;
}
.gpt3__possibility-content p:last-child{
    color: #F49867;
    font-family: var(--font-family);
}
.gpt3__possibility-content p{
    color: var(--color-text);
    font-family: var(--font-family);
    margin-top: 2rem;
    font-size: 16px;
    line-height: 30px;
}
.gpt3__possibility-content h1{
    margin-top: 1rem;
}
@media screen and (max-width:900px) {
    .gpt3__possibility-content h1{
        font-size: 25px;
    }
    .gpt3__possibility-content p{
        font-size: 14px;
        line-height: 20px;
        margin-top: 1rem;
    }
}
@media screen and (max-width:760px) {
    .gpt3__possibility{
        padding: 2rem 3rem;
        display: flex;
        flex-direction: column;
    }
    .gpt3__possibility-image img{
        width: 80%;
        height: 80%;
        
        display: flex;
    }
    .gpt3__possibility-content{
        margin-top: 1rem;
        margin-left: 0;
        
    }
}