.gpt3__blog{
    display: flex;
    flex-direction: column;
    padding: 4rem 6rem;
}
.gpt3__blog-container{
    display: flex;
    flex-direction: row;
}
.gpt3__blog-groupA{
    flex: 0.75;
    margin-right: 2rem;
}
.gpt3__blog-groupB{
    flex: 1;
    display: grid;
    grid-gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
}
.gpt3__blog-heading h1{
    margin-bottom: 4rem;
    max-width: 500px;
}
@media  screen and (max-width:760px) {
    .gpt3__blog{
        padding: 2rem 3rem;
    }
    .gpt3__blog-container{
        display: flex;
        flex-direction: column;
    }
    .gpt3__blog-groupA{
     margin-right: 0;
     margin-bottom: 2rem;
    }
    .gpt3__blog-groupB{
        grid-gap: 1rem;
    }
    .gpt3__blog-heading h1{
        text-align: center;
        font-size: 25px;
    }
}