.gpt3__footer{
    padding: 4rem 6rem 1rem 6rem;
}
.gpt3__footer-heading h1{
    max-width:500px ;
    margin: 0 auto;
    text-align: center;
    font-family: var(--font-family);
}
.gpt3__footer-heading p{
    margin-top: 2rem;
    text-align: center;
    color: #FF8A71;
    font-family: var(--font-family);
    cursor: pointer;
}
.gpt3__footer-container{
    display: flex;
    flex-direction: row;
    margin-top: 2rem;
}
.gpt3__footer-content{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}
.gpt3__footer-container-heading h3{
    color: #FFF;
    font-size: 30px;
    font-family: var(--font-family);
}
.gpt3__footer-container-heading p{
    color: #fff;
    font-size: 10px;
    max-width: 120px;
}
.gpt3__footer-link h6{
    color: var(--color-text);
    font-family: var(--font-family);
    font-size: 16px;
}
.gpt3__footer-link p{
    font-family: var(--font-family);
    color: #fff;
    font-size: 12px;
    margin-top: 1rem;
    cursor: pointer;
}
.gpt3__footer-copyright p{
    text-align: center;
    color: #FF8A71;
    margin-top: 2rem;
    font-size: 12px;
    font-family: var(--font-family);
}
@media screen and (max-width:900px) {
    .gpt3__footer-container{
        justify-content: space-between;
    }
}
@media screen and (max-width:760px) {
   
    .gpt3__footer{
        padding: 3rem 3rem 1rem 3rem;
    }
    .gpt3__footer-container{
        display: flex;
        flex-direction: column;
    }
    .gpt3__footer-container-heading p{
        max-width: 100%;
        margin-bottom: 2rem;
        margin-top: 1rem;
    }
    .gpt3__footer-container-heading{
        margin: 0 auto;
        text-align: center;
    }
    .gpt3__footer-content{
        justify-content: space-between;
    }
    .gpt3__footer-content h6{
        font-size: 12px;
    }
    .gpt3__footer-content p{
        font-size: 10px;
    }
}
@media screen and (max-width:450px) {
    .gpt3__footer-content{
        display: flex;
        flex-direction: column;
    }
    .gpt3__footer-link{
        margin: 0 auto;
        text-align: center;
        margin-top: 1rem;
    }
    .gpt3__footer-link p{
        font-size: 12px;
        margin-top: 0.5rem;
    }
    .gpt3__footer-link h6{
        font-size: 16px;
    }
    .gpt3__footer-container-heading p{
        margin-bottom: 1rem;
    }
    .gpt3__footer-heading h1{
        font-size: 25px;
    }
}
