*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}
body{
    background-color: var(--color-bg);
}
a{
    text-decoration: none;
    color: unset;
}
.gradient__bg{
     /* ff 3.6+ */
     background:-moz-radial-gradient(circle at 0% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);

     /* safari 5.1+,chrome 10+ */
     background:-webkit-radial-gradient(circle at 0% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);
 
     /* opera 11.10+ */
     background:-o-radial-gradient(circle at 0% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);
 
     /* ie 10+ */
     background:-ms-radial-gradient(circle at 0% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);
 
     /* global 92%+ browsers support */
     background:radial-gradient(circle at 0% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);
}
.gradient__text{
    background: var(--gradient-text);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color:transparent;
}
.section-padding{
    padding: 4rem 6rem;
}
.section-margin{
    margin: 4rem 6rem;
}
.scale-down-center {
	-webkit-animation: scale-down-center 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
	        animation: scale-down-center 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
@-webkit-keyframes scale-down-center {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    100% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
}
@keyframes scale-down-center {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    100% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
}
@media screen and(max-width:700px){
    .section-padding{
        padding: 4rem;
    }
    .section-margin{
        margin: 4rem;
    }
}
@media  screen and (max-width:550px) {
    .section-padding{
        padding: 4rem 2rem;
    }
    .section-margin{
        margin: 4rem 2rem;
    }
}
  