.gpt3__article{
    display: flex;
    flex-direction: column;
    background-color: var(--color-footer);
    width: 100%;
    height: 100%;
}
.gpt3__article-blog-image{
    width: 100%;
    height: 100%;
    background-color: var(--color-bg);
}
.gpt3__article-blog-image img{
    width: 100%;
    height: 100%;
}
.gpt3__article-blog_content{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1rem 1.5rem;
    height: 100%;
}
.gpt3__article-blog_content p{
    font-size: 12px;
    font-family: var(--font-family);
    color: var(--color-text);
    font-weight: 400;
    margin-bottom: 1rem;
}
.gpt3__article-blog_content h5{
    font-size: 14px;
    font-family: var(--font-family);
    color: var(--color-text);
    font-weight: 600;
    margin-bottom: 1rem;
}
.gpt3__article-blog_content p:last-child{
    cursor: pointer;
    margin-bottom: 0;
    margin-top: 2rem;
    font-size: 12px;
}
@media screen and (max-width:760px) {
    .gpt3__article{
        display: flex;
        flex-direction: column;
    }
}
@media screen and (max-width:450px){
    .gpt3__article-blog_content h5{
        font-size: 12px;
    }
    .gpt3__article-blog_content p:last-child{
        font-size: 10px;
        margin-top: 0;
    }
}