.gpt3__CTA{
    border-radius: 1rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 2rem;
    margin: 3rem 6rem;
    background: var(--gradient-bar);
}
.gpt3__CTA-content h4{
    font-family: var(--font-family);
    margin-top: 0.5rem;
}
.gpt3__CTA-content p{
    font-size: 14px;
    font-family: var(--font-family);
}
.gpt3__CTA-btn button{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.5rem 1rem;
    background-color: black;
    color: #fff;
    border-radius: 40px;
    border: none;
    outline: none;
    cursor: pointer;
    min-width: 150px;
    font-size: 18px;
}
@media screen and (max-width:760px){
    .gpt3__CTA{
        display: flex;
        flex-direction: column;
        margin:3rem 3rem ;
    }
    .gpt3__CTA-btn {
        margin-top: 1rem;
        float: right;
    }
    .gpt3__CTA-content h4{
        font-size: 16px;
    }
    .gpt3__CTA-content p{
        font-size: 12px;
    }
}
@media screen and (max-width:450px) {
    .gpt3__CTA-btn button{
        min-width: 120px;
        font-size: 14px;
    }
}