.whatgpt3{
    margin: 4rem  6rem;
    background-color: var(--color-blog);
    padding: 3rem;
}
.whatgpt3__question-heading{
    display: flex;
    justify-content: space-between;
    
}
.whatgpt3__feature{
    display: flex;
    flex-direction: row;
    margin-top: 3rem;
}
.whatgpt3__heading{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.whatgpt3__heading h1{
    width: 580px;
    margin-top: 3rem;
    margin-right: 1rem;
}
.whatgpt3__library {
    color: #FF8A71;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    float: right;
    margin-top: 1rem;
    cursor: pointer;
}
.whatgpt3__question p{
    width: 680px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: var(--color-text);
}
.whatgpt3__question h1{
    width: 173px;
    margin-right: 1rem;
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 18px;
    line-height: 24px;
    letter-spacing: -0.04em;
    color: #fff;
}
.whatgpt3__question .whatgpt3__line{
    width: 38px;
    height: 3px;
    background: var(--gradient-bar);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 0.25rem;
}
@media screen and (max-width:760px){
    .whatgpt3{
        margin: 3rem 0;
        padding: 3rem 3rem;
    }
    .whatgpt3__feature{
        display: flex;
        flex-direction: column;
        margin-top: 1rem;
    }
    .whatgpt3__question-heading{
        display: flex;
        flex-direction: column;
    }
    .whatgpt3__question-heading h1{
        width: auto;
        margin-right: 0;
    }
    .whatgpt3__question p{
        line-height: 20px;
        font-size: 12px;
        width: auto;
        margin-top: 1rem;
    }
    .whatgpt3__heading h1{
        width: auto;
        font-size: 25px;
        margin-right: 0;
    }
    
    .whatgpt3__library {
        font-size: 12px;
        line-height: 20px;
    }
}
@media screen and (max-width:430px) {  
   .whatgpt3__heading h1{
       font-size: 20px;
   } 
}