.gpt3__navbar{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    padding: 2rem 6rem;
}
.gpt__navbar-links{
    flex: 1;
    display: flex;
    flex-direction: row;
    align-items: center;
}
.gpt3__navbar-links_logo{
    margin-right: 2rem;
}
.gpt3__navbar-links_logo img{
    width: 63.56px;
    height: 17.04px;
    align-items: center;
    display: flex;
}
.gpt3__navbar-links_container{
    display: flex;
    flex-direction: row;
}
.gpt3__navbar-signin{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.gpt3__navbar a{
    color: #ffff;
    font-weight: 500;
    font-family: var(--font-family);
    font-size: 16px;
    line-height: 24px;
    text-transform: capitalize;
    margin: 0 1.5rem;
    cursor: pointer;
}
.gpt3__navbar-button{
    padding: 0.3rem 1rem;
    background-color: #FF4820;
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    border: none;
    border-radius: 5px;
    text-transform: capitalize;
    cursor: pointer;
    margin-right: 1rem;
}
.gpt3__navbar-signin p,
.gpt3__navbar-toggle_container p{
    color: #fff;
    font-weight: 500;
    font-family: var(--font-family);
    font-size: 16px;
    line-height: 24px;
    text-transform: capitalize;
    margin: 0 1rem;
}
.gpt3__navbar-toggle{
    display: none;
    position: relative;
}
.gpt3__navbar-toggle_container{
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background-color: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5px rgb(0,0,0,0.2);
}
.gpt3__navbar-toggle_links a{
    line-height: 35px;
}
.gpt3__navbar-toggle_links{
    display: flex;
    flex-direction: column;
}
.gpt3__navbar-toggle_button{
    display: none;
}
@media screen and (max-width:1095px) {
    .gpt3__navbar{
        padding: 2rem 3rem;
    }
    .gpt3__navbar a{
        margin: 0 1rem;
    }
    .gpt3__navbar-links_logo{
        margin-right: 1.5rem;
    }
}
@media screen and (max-width:905px) {
    .gpt3__navbar-links_container{
        display: none;
    }
    .gpt3__navbar-toggle{
        display: flex;
    }
}
@media screen and (max-width:550px) {
    .gpt3__navbar-signin{
        display: none;
    }
    .gpt3__navbar-toggle_button{
        display: block;
        margin-top: 2rem;
    }
    .gpt3__navbar-toggle_button p{
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .gpt3__navbar-button{
        margin: 0;
        margin-top: 0.5rem;
    }
    .gpt3__navbar{
        padding: 2rem;
    }
    
}