.gpt3__header{
    display: flex;
    margin: 0 6rem;
}
.gpt3__header-content{
    display: flex;
    flex-direction: column;
    align-items: flex-start; 
    
}
.gpt3__header-content h1{
    font-family: var(--font-family);
    font-size: 60px;
    letter-spacing: -0.04em;
    font-weight: 800;             
}
.gpt3__header-content p{
    font-family: var(--font-family);
    font-size: 16px;
    font-weight: normal;
    color: var(--color-text);
    margin-top: 2rem;
    letter-spacing: 2px;
    line-height: 20px;
}
.gpt3__header-image img{
    width: 100%;
    height: auto;
}
.gpt3__header-content_input{
    display: flex;
    flex-direction: row;
    margin-top: 2rem;
}
.gpt3__header-content_input input{
    flex: 2;
    width: 100%;
    min-height: 40px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 28px;
    background: var(--color-footer);
    border: 2px solid var(--color-footer);
    padding: 0 1rem;
    outline: none;
    color: #fff;

    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}
.gpt3__header-content_input button{
    
    flex: 1;
    width: 100%;
    min-height: 40px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 28px;
    background: #FF4820;
    border: 2px solid #FF4820;
    padding: 0 1rem;
    color: #fff;
    cursor: pointer;
    outline: none;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}
.gpt3__header-people{
    display: flex;
    margin-top: 2rem;
    margin-bottom: 1rem;
}
.gpt3__header-people p{
    margin-left: 1rem;
    font-size: 14px;
    margin-top: 0;
    letter-spacing: 0;
}.gpt3__header-people img{
    width: 182px;
    height: 39px;
}

@media screen and (max-width:1095px){
    .gpt3__header{
        margin: 0 3rem;
    }
    .gpt3__header-people{
        display: flex;
    }
    .gpt3__header-people p{
        
        margin-left: 1rem;
        width: 100%;
    }
    .gpt3__header-content h1{
        font-size: 50px;
    }
}
@media screen and (max-width:1000px){
    .gpt3__header-content_input button{
        font-size: 16px;
    }
    .gpt3__header-content h1{
        font-size: 40px;
    }
    .gpt3__header-people p{
        width: 200px;
    }
}
@media screen and (max-width:900px){ 
    
    .gpt3__header-people p{
        width: 200px;
    }
    .gpt3__header-image img{
        width: 100%;
        height: auto;
    }
    .gpt3__header-content_input input{
        min-height: 30px;
        font-size: 14px;
    }
    .gpt3__header-content_input button{
        min-height: 30px;
        font-size: 16px;
    }
    .gpt3__header-content p{
        font-size: 13px;
    }
}
@media  screen and (max-width:810px) {
    .gpt3__header-content h1{
        font-size: 30px;
    }
    .gpt3__header-content p{
        font-size: 12px;
    }
    .gpt3__header-content_input input{
        font-size: 14px;
        line-height: 18px;
        min-height: 25px;
    }
    .gpt3__header-content_input button{
        font-size: 14px;
        line-height: 18px;
        min-height: 25px;
    }
}
@media screen and (max-width:760px) {
    .gpt3__header{
        display: flex;
        flex-direction: column;
    }
    .gpt3__header-content{
        display: flex;
        flex-direction: column;
    }
    .gpt3__header-content h1{
        font-size: 40px;
        text-align: center;
    }
    .gpt3__header-content p{
        display: none;
    }
    .gpt3__header-content_input{
        margin: 0 auto;
        margin-top: 2rem;
    }
    .gpt3__header-people{
        display: flex;
        flex-direction: column;
        margin: 0 auto;
        margin-top: 2rem;
    }
    .gpt3__header-people p{
        width: 100%;
        margin-top: 0.5rem;
    }
    .gpt3__header-image img{
        margin-top: 2rem;
    }
}
@media screen and (max-width:450px) {
    .gpt3__header-content h1{
        font-size: 30px;
    }
    .gpt3__header{
        margin: 0 2rem;
    }
}