
.gpt3__feature-title{
    max-width: 180px;
}
.gpt3__feature-title h1{
    width: 170px;
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 18px;
    line-height: 24px;
    letter-spacing: -0.04em;
    color: #fff;
}
.gpt3__feature-title div{
    width: 38px;
    height: 3px;
    background: var(--gradient-bar);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 0.25rem;
}

.gpt3__feature-text p{
    margin-right: 1rem;
    margin-top: 2rem;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: var(--color-text);
}
@media  screen and (max-width:760px) {
    .gpt3__feature{
        margin-top: 2rem;
    }
    .gpt3__feature-text p{
        margin-top: 1rem;
    }
    .gpt3__feature-text p{
        margin-right: 0;
        margin-top: 1rem;
        font-size: 12px;
        line-height: 20px;
    }
}
